export default {
	data() {
		return {
			commdity: [], //商品
			slideshow: [], //轮播
			show: false,
			popupimg:''
		}
	},
	onLoad(news) {
		// console.log(news)
		var item = JSON.parse(decodeURIComponent(news.news));
		console.log(item)
		this.commdity = item;
		this.slideshow = item.slideshow
		console.log(this.slideshow)
		this.popupimg = item.slideshow[0].slideshow
		// console.log(this.popupimg)
	},
	methods: {
		buy() {
			this.show = !this.show
		},
		onClose() {
			this.show = false
		}
	}
}
